import { Component } from "react";

const Layout = (NewComponent) => {
  return class BindLayout extends Component {
    constructor(props) {
      super(props);
      this.state = {
        name: "sanat gupta"
      };
    }
    render() {
      return <NewComponent props={this.state}></NewComponent>;
    }
  };
};
export default Layout;
