import "./styles.css";
import useCounter from "./useCounter";
import Layout from "./withLayOut";
function App() {
  const { count, handleDecreseCounter, handleIncreseCounter } = useCounter();
  return (
    <div className="App">
      <h1>Custom Hook</h1>
      <h2>{count}</h2>
      <button onClick={handleIncreseCounter}>Increse</button>
      <button onClick={handleDecreseCounter}>Decrese</button>
    </div>
  );
}

export default Layout(App);
